export default HireEthereumDevelopers;
export const queryData = graphql`
  query HireEthereumDevelopersPageQuery {
    HireEthereumDevelopersImg: file(
      relativePath: { eq: "hire-ethereum-developers.png" }
    ) {
      childImageSharp {
        gatsbyImageData
      }
    }
    heroImage: file(
      relativePath: { eq: "hirescreens/devPages/ethereum-developers-hero.webp" }
    ) {
      childImageSharp {
        gatsbyImageData(
          layout: CONSTRAINED
          width: 1200
          quality: 90
          placeholder: BLURRED
        )
      }
    }

    site {
      siteMetadata {
        firestore {
          apiKey
          appId
          authDomain
          messagingSenderId
          projectId
          storageBucket
        }
      }
    }

    allMetaJson(filter: { slug: { eq: "hire-ethereum-developers" } }) {
      nodes {
        metaTitle
        metaKeyword
        title
        slug
        description
      }
    }
  }
`;
